* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}
.valid {
  background-color: green;
}
.timer {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  font-weight: bold;
}
.field:not(:last-child) {
  margin-right: 30px;
}
.value {
  font-weight: bold;
  font-size: xx-large;
  color: red;
}
#timer {
  display: flex;
  justify-content: center;
}
#datetime-picker {
  height: 40px;
  border-radius: 5px;
}
button {
  width: 60px;
  margin-left: 10px;
  border-radius: 5px;
}
